import { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../Auth.module.css";
import NotifyEvent from "../../../Utils/Snackbar/NotifyEvent";
import { assestsAuthUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useAuthInstituteNumber } from "../../../Hooks/ApiHooks/Auth/auth-api";
import { useNavigate } from "react-router-dom";
import { decryption } from "../../../Services/Encryption/decrypt";
import AuthFormFieldWithIcon from "../../../Ui/Auth/AuthFormFieldWithIcon";
import ButtonWithTypeSubmit from "../../../Ui/Button/ButtonWithTypeSubmit";
import GetStarted from "../../GetStarted";

const InstituteMobileRegister = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const [state, setState] = useState({
    insPhoneNumber: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");
  const [snackbar, setSnackbar] = useState({
    open: false,
    msg: "",
    variant: "error",
  });
  const [authInstituteNumber] = useAuthInstituteNumber();
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onSubmitHandler = (e) => {
    e.preventDefault();
    if (state.insPhoneNumber) {
      setDisabled((pre) => !pre);
      authInstituteNumber({
        phone: {
          insPhoneNumber: `${state.insPhoneNumber}`,
          status: "Not Verified",
        },
      })
        .then((res) => {
          let response = decryption(res?.data?.encrypt);
          if (response?.insPhoneNumber) {
            navigate("/institute/verify", {
              state: {
                phoneNumber: response?.insPhoneNumber,
              },
            });
            setDisabled((pre) => !pre);
          }
        })
        .catch(() => {
          setSnackbar({
            open: true,
            msg: "Time out of request...",
            variant: "error",
          });
        });
    } else {
      setErrorField({
        insPhoneNumber: "* required",
      });
    }
  };

  const onNavigate = () => {
    navigate("/");
  };
  return (
    <>
      <GetStarted>
        <div className={style.auth_card_wrapper}>
          <div className={style.auth_create_container}>
            <div>
              <h6
                className={style.auth_card_title}
                style={{
                  fontSize: "1rem",
                }}
              >
                {t("signup")}
              </h6>
            </div>
          </div>
          <div
            className={style.auth_login_form}
            style={{
              marginTop: "0",
            }}
          >
            <form onSubmit={onSubmitHandler}>
              <AuthFormFieldWithIcon
                label={t("mobile_number")}
                placeholder={t("institute_mobile_number_placeholder")}
                fieldIconUrl={`${assestsAuthUrl}/auth-flag.svg`}
                validate={errorField["insPhoneNumber"]}
                name="insPhoneNumber"
                value={state.insPhoneNumber}
                onChange={onInputChange}
              />

              <p className={style.auth_privacy_and_policy}>
                {t("mobile_login_note")}{" "}
                <span className={style.auth_privacy_and_policy_link}>
                  <a href="/q/terms/condition">{t("terms_condition")}</a>
                </span>{" "}
                {t("and")}
                <span className={style.auth_privacy_and_policy_link}>
                  <a href="/q/privacy/policy">{t("privacy_policy")}</a>
                </span>
              </p>

              <ButtonWithTypeSubmit
                type="submit"
                buttonText={t("send_otp")}
                disabled={disabled}
                customStyleButton={{
                  marginTop: "1.5rem",
                }}
              />
              <div className={style.auth_footer_container}>
                <p className={style.auth_forget_text}>
                  <span
                    style={{
                      color: "rgba(0,0,0,.8)",
                    }}
                  >
                    {t("already_registered")}
                  </span>
                  <span onClick={onNavigate}> {t("login")}</span>
                </p>
              </div>
            </form>
          </div>
        </div>
      </GetStarted>
      <NotifyEvent eventState={snackbar} eventStateHandler={setSnackbar} />
    </>
  );
};

export default InstituteMobileRegister;
